import Vue from 'vue';
import Router from 'vue-router';

import Home from '@/pages/Home';
import PrivacyPolicy from '@/pages/PrivacyPolicy';
import ReceiptButler from '@/pages/ReceiptButler';

Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/',
            name: 'Home',
            component: Home,
        },
        {
            path: '/privacy',
            name: 'PrivacyPolicy',
            component: PrivacyPolicy,
        },
        {
            path: '/receiptButler',
            name: 'ReceiptButler',
            component: ReceiptButler,
        },
    ],
});



